

export class basicEnemy{
  constructor(health){
    this.health = health
  }
}

export class bat extends basicEnemy{
  constructor(){
    super(12)
    this.name = "bat"
    generateEnemyElement(this)

  }
}

export class tiger extends basicEnemy{
  constructor(){
    super(89)
    this.name = "tiger"
    generateEnemyElement(this)

  }
}

export class bruv extends basicEnemy{
  constructor(){
    super(33)
    this.name = "bruv"
    generateEnemyElement(this)
  }
}

export class g extends basicEnemy{
  constructor(){
    super(2)
    this.name = ""
    generateEnemyElement(this)
  }
}


function generateEnemyElement(enemy) {
 const enemyModal = document.createElement("div")
 const enemySelector = document.createElement("div")

 const graphix = document.querySelector(`.g${enemy.name}`).cloneNode(true)

 enemySelector.classList.add("enemySelector")

 enemyModal.classList.add("enemy")
 enemyModal.classList.add(enemy.name)
 enemyModal.append(graphix)
 enemyModal.append(enemySelector)

 enemyModal.entity = enemy
 enemy.modal = enemyModal
  
}